import {HttpClient} from '@angular/common/http';
import {LoginIp} from '../model/LoginIp';
import {Injectable} from '@angular/core';
import {LoginService} from './login.service';
import {Subject} from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class LoginIpService {

    public loginIp$: Subject<LoginIp> = new Subject<LoginIp>();
    public loginInfo: LoginIp = null;
    constructor(private http: HttpClient, private loginService: LoginService) {
    }

    public getLoginIp() {
        this.http.get<LoginIp>(this.loginService.getLoginIpUrl()).subscribe((loginIp: LoginIp) => {
            console.log('Got login ip: ' + loginIp);
            this.loginInfo = loginIp;
            this.loginIp$.next(loginIp);
        }, (e) => {
            console.log('Could not get login ip: ' + e);
            this.loginInfo = null;
            this.loginIp$.next(null);
        });
    }
}
