import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LoginConfigService {

  private title: string;
  private loginUrl: string;
  private loginApiUrl: string;
  private loginIpUrl: string;
  private logoutUrl: string;
  private logoSrc: string;
  private appid: string;

  public getAppid(): string {
    return this.appid;
  }

  public setAppid(value: string): void {
    this.appid = value;
  }

  public getTitle(): string {
    return this.title;
  }

  public setTitle(value: string): void {
    this.title = value;
  }

  public getLoginUrl(): string {
    return this.loginUrl;
  }

  public setLoginUrl(value: string): void {
    this.loginUrl = value;
  }

  public getLoginApiUrl(): string {
    return this.loginApiUrl;
  }

  public setLoginApiUrl(value: string): void {
    this.loginApiUrl = value;
  }

  public getLoginIpUrl(): string {
    return this.loginIpUrl;
  }

  public setLoginIpUrl(value: string): void {
    this.loginIpUrl = value;
  }

  public getLogoutUrl(): string {
    return this.logoutUrl;
  }

  public setLogoutUrl(value: string): void {
    this.logoutUrl = value;
  }

  public getLogoSrc(): string {
    return this.logoSrc;
  }

  public setLogoSrc(value: string): void {
    this.logoSrc = value;
  }
}
